
import { defineComponent, ref, watchEffect } from "vue";
import { mapGetters } from "vuex";
export default defineComponent({
  name: "Navbar",
  props: {
    category: {
      type: String,
      default: "general",
    },
    keyword: {
      type: String,
      default: "",
    },
  },
  computed: mapGetters(["favorite", "isMobile"]),
  setup(props) {
    // 選單陣列
    const tabList = ref([
      "general",
      "business",
      "entertainment",
      "health",
      "science",
      "sports",
      "technology",
    ]);
    // 關鍵字
    let searchKeyWord = ref("");
    // 關鍵字debounce timer
    const searchDebounceTimer = ref(0 as ReturnType<typeof setTimeout>);
    // 收合選單
    const toggleMenu = ref(false);
    // 監聽搜尋
    watchEffect(() => {
      searchKeyWord.value = props.keyword;
    });
    return {
      tabList,
      searchKeyWord,
      searchDebounceTimer,
      toggleMenu,
    };
  },
  mounted() {
    const _ = this;
    // 監聽點擊選單以外的動作
    document.body.addEventListener("click", () => {
      _.toggleMenu = false;
    });
  },
  methods: {
    /**
     * 處理點擊分類事件
     *
     * @param {string} category - 分類
     */
    handleClickTab(event: Event, category: string, isActive: boolean) {
      event.stopPropagation();
      this.toggleMenu = isActive ? true : false;
      this.$emit("change:tab", category);
    },

    /**
     * 處理搜尋事件
     */
    handleSearch() {
      const _ = this;
      // 簡單debounce
      if (this.searchDebounceTimer) {
        clearTimeout(this.searchDebounceTimer);
      }
      this.searchDebounceTimer = setTimeout(() => {
        _.$emit("change:input", this.searchKeyWord);
      }, 300);
    },
  },
});
